import { Fragment, useEffect, useState } from 'react';
import { DateTimeDisplay, Card, CardHeader, CardContent, Flex } from '@pega/cosmos-react-core';
import PropTypes from 'prop-types';
import App from './App';

// includes in bundle
import Operator from './Operator.jsx';

import StyledPegaExtensionsScrumboardWrapper from './styles';

// Duplicated runtime code from Constellation Design System Component

// props passed in combination of props from property panel (config.json) and run time props from Constellation
// any default values in config.pros should be set in defaultProps at bottom of this file
export default function PegaExtensionsScrumboard(props) {
  const {
    getPConnect,
    title,
    label,
    createLabel,
    updateLabel,
    createOperator,
    updateOperator,
    createDateTime,
    updateDateTime,
    resolveLabel,
    resolveOperator,
    resolveDateTime,
    hideLabel
  } = props;

  const pConn = getPConnect();
  const actions = pConn.getActionsApi();

  const [_label, user, dateTimeValue] =
    label === 'Create operator'
      ? [createLabel, createOperator, createDateTime]
      : label === 'Update operator'
      ? [updateLabel, updateOperator, updateDateTime]
      : [resolveLabel, resolveOperator, resolveDateTime];

  const [isLoading, setIsLoading] = useState(true);
  const [items, setItems] = useState([]);

  useEffect(() => {
    setIsLoading(true);
    const dataViewName = 'D_AllSprintItems';
    const parameters = {
      SprintInsKey: 'PEGAPROJMGMT-WORK SPR-63051'
    };

    const context = 'app/primary_1';
    PCore.getDataPageUtils()
      .getDataAsync(dataViewName, context, parameters)
      .then(response => console.log(response))
      //.then(data => setItems(data))
      .catch(error => {
        console.log(error);
      });
  }, []);

  return (
    <StyledPegaExtensionsScrumboardWrapper>
      <App></App>
    </StyledPegaExtensionsScrumboardWrapper>
  );
}

PegaExtensionsScrumboard.defaultProps = {
  label: 'Create operator',
  title: 'Create operator',
  createLabel: null,
  updateLabel: null,
  createOperator: null,
  updateOperator: null,
  createDateTime: null,
  updateDateTime: null,
  resolveLabel: null,
  resolveOperator: null,
  resolveDateTime: null,
  hideLabel: false
};

PegaExtensionsScrumboard.propTypes = {
  getPConnect: PropTypes.func.isRequired,
  label: PropTypes.string,
  title: PropTypes.string,
  createLabel: PropTypes.string,
  updateLabel: PropTypes.string,
  resolveLabel: PropTypes.string,
  createOperator: PropTypes.objectOf(PropTypes.any),
  updateOperator: PropTypes.objectOf(PropTypes.any),
  resolveOperator: PropTypes.objectOf(PropTypes.any),
  createDateTime: PropTypes.string,
  updateDateTime: PropTypes.string,
  resolveDateTime: PropTypes.string,
  hideLabel: PropTypes.bool
};

